<template>
  <div id="book_page" class="book_page">
<!--    <div v-if="global.mode === 0" class="pre_arrow_part" @click="autoLeftToRight"></div>-->
    <v-touch
    @swipeup="autoRightToLeft"
    @swipedown="autoLeftToRight">
      <div class="body_part">
        <div class="books">
          <div class="about_page">
            <div class="page_left" @click="autoLeftToRight">
              <img v-if="pageRotateSrc.L1" :src="pageLeftSrc.L1"
                   :style="'transform: translateX('+floatLeftValue * animeFloatL1+'rem);'"
                   class="img_left"/>
              <img v-if="pageRotateSrc.L2" :src="pageLeftSrc.L2"
                   :style="'transform: translateX('+floatLeftValue * animeFloatL2+'rem);'"
                   class="img_left"/>
              <img v-if="pageRotateSrc.L3" :src="pageLeftSrc.L3"
                   :style="'transform: translateX('+floatLeftValue * animeFloatL3+'rem);'"
                   class="img_left"/>
              <img v-if="pageRotateSrc.L4" :src="pageLeftSrc.L4"
                   :style="'transform: translateX('+floatLeftValue * animeFloatL4+'rem);'"
                   class="img_left"/>
            </div>
            <!--TODO: 这里为了避免左翻页会产生闪动情况，计划分别使用两个翻页做效果，但是无用-->
            <div :class="pageRotate >= -90 ? 'page_rotate':'page_rotate_left'"
                 :style="'transform: rotateY(' + pageRotate + 'deg)'" @click="autoRightToLeft">
              <img
                :style="pageRotate >= -90 ? 'opacity:'+(-pageRotate)/90*shadowMax : 'transform: scaleX(1); opacity:'+(180+pageRotate)/90*shadowMax"
                class="page_shadow"
                src="/static/book/page_shadow.png"/>
              <img v-if="pageRotateSrc.L1" :src="pageRotateSrc.L1"
                   :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL1+'rem);' : -floatRightValue * animeFloatL1+'rem) scaleX(-1);')"
                   class="img_right"/>
              <img v-if="pageRotateSrc.L2" :src="pageRotateSrc.L2"
                   :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL2+'rem);' : -floatRightValue * animeFloatL2+'rem) scaleX(-1);')"
                   class="img_right"/>
              <img v-if="pageRotateSrc.L3" :src="pageRotateSrc.L3"
                   :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL3+'rem);' : -floatRightValue * animeFloatL3+'rem) scaleX(-1);')"
                   class="img_right"/>
              <img v-if="pageRotateSrc.L4" :src="pageRotateSrc.L4"
                   :style="'transform: translateX('+(pageRotate >= -90 ? floatLeftValue * animeFloatL4+'rem);' : -floatRightValue * animeFloatL4+'rem) scaleX(-1);')"
                   class="img_right"/>
            </div>
            <div class="page_right" @click="autoRightToLeft">
              <img v-if="pageRotateSrc.L1" :src="pageRightSrc.L1"
                   :style="'transform: translateX('+floatRightValue * animeFloatL1+'rem);'"
                   class="img_right"/>
              <img v-if="pageRotateSrc.L2" :src="pageRightSrc.L2"
                   :style="'transform: translateX('+floatRightValue * animeFloatL2+'rem);'"
                   class="img_right"/>
              <img v-if="pageRotateSrc.L3" :src="pageRightSrc.L3"
                   :style="'transform: translateX('+floatRightValue * animeFloatL3+'rem);'"
                   class="img_right"/>
              <img v-if="pageRotateSrc.L4" :src="pageRightSrc.L4"
                   :style="'transform: translateX('+floatRightValue * animeFloatL4+'rem);'"
                   class="img_right"/>
            </div>
          </div>
        </div>
      </div>
    </v-touch>

<!--    <div v-if="global.mode === 0" class="next_arrow_part" @click="autoRightToLeft"></div>-->
    <LangTransfer></LangTransfer>
    <Logo :canBeHidden=false></Logo>
    <Menu></Menu>
  </div>
</template>

<style scoped>

div.book_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

div.body_part {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  width: calc(var(--book_width));
  height: 100%;
  display: flex;
  align-items: center;
  justify-items: center;
}

div.books {
  display: inline-block;
  width: calc(var(--book_width));
  height: calc(var(--book_height));
}

div.about_page {
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: center;
  width: calc(var(--book_width));
  height: calc(var(--book_height));
  perspective: 2500px;
  transform-style: preserve-3d;
}

div.page_left {
  transform-origin: right;
  position: absolute;
  width: calc(var(--book_width) / 2);
  height: calc(var(--book_height));
  overflow: hidden;
  border-bottom-left-radius: 6.161%;
  border-top-left-radius: 6.161%;
  cursor: url("/static/book/pre_arrow.png"), pointer;
}

div.page_rotate {
  transform-origin: left;
  position: absolute;
  right: 0;
  width: calc(var(--book_width) / 2);
  height: calc(var(--book_height));
  overflow: hidden;
  border-bottom-right-radius: 6.161%;
  border-top-right-radius: 6.161%;
  z-index: 1;
  cursor: url("/static/book/next_arrow.png"), pointer;
}

div.page_rotate_left {
  position: absolute;
  left: 0;
  overflow: hidden;
  border-bottom-right-radius: 6.161%;
  border-top-right-radius: 6.161%;
  width: calc(var(--book_width));
  height: calc(var(--book_height));
  cursor: url("/static/book/pre_arrow.png"), pointer;
}

img.page_shadow {
  position: absolute;
  width: calc(var(--book_width));
  height: calc(var(--book_height));
  z-index: 1;
}

div.page_right {
  transform-origin: left;
  position: absolute;
  right: 0;
  width: calc(var(--book_width) / 2);
  height: calc(var(--book_height));
  overflow: hidden;
  border-bottom-right-radius: 6.161%;
  border-top-right-radius: 6.161%;
  cursor: url("/static/book/next_arrow.png"), pointer;
}

img.img_left {
  position: absolute;
  left: 0;
  width: calc(var(--book_width));
  height: calc(var(--book_height));
}

img.img_right {
  position: absolute;
  right: 0;
  width: calc(var(--book_width));
  height: calc(var(--book_height));
}

/*手机版样式*/
@media screen and (max-width: 540px) {
  div.body_part {
    position: fixed;
    /*left: 25rem;*/
    top: 120rem;
    width: 700rem;
    height: calc(var(--screen_height) - 120rem);
    display: unset;
  }

  div.books {
    transform-origin: top left;
    transform: translate(calc(var(--book_height) + (700rem - var(--book_height)) / 2), calc((var(--screen_height) - 120rem - var(--book_width)) / 2)) rotate(90deg);
  }
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";
import Footer from "../../components/footer/Footer";
import Logo from "../../components/logo/Logo"
import {dev} from "../../../config";
// when flipAnimeTask is null means anime finished
let flipAnimeTask

//export default
export default {
  name: "Stories",
  data() {
    return {
      global: global,
      pageRotate: 0,
      curPageIdx: 0,
      tarPageIdx: 0,
      pageLeftSrc: null,
      pageRotateSrc: null,
      pageRightSrc: null,
      pageList: [], //页面布局信息
      totalPageNum: 9,//默认多少页书||增加或删除页面，需要改变这个页数的值||
      shadowMax: 0.2,//页面翻转阴影最大值
      animeMaxLength: 660,//图层浮动的最大范围
      animeFloatL1: 0,//L1层的浮动比例
      animeFloatL2: 0.2,//L2层的浮动比例
      animeFloatL3: 0.8,//L3层的浮动比例
      animeFloatL4: 0.4,//L4层的浮动比例
      floatLeftValue: 0,//左半部分浮动值
      floatRightValue: 0,//右半部分浮动值
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer,
    Logo
  },
  beforeCreate() {
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
    this.init()
    this.resize_book()
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
        this.resize_book()
      })();
    };
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  watch: {
    // Listen the page rotate event and then change the img and the show mode
    pageRotate: function (pageRotate, oldPageRotate) {
      // from left to right
      if (oldPageRotate > pageRotate && oldPageRotate - pageRotate < 179) {
        if (pageRotate === -180) {
          this.curPageIdx += 1
          this.pageRotate = 0
        }
      }
      // form right to left
      else if (oldPageRotate < pageRotate && pageRotate - oldPageRotate < 179) {
        if (oldPageRotate === -180) {
          this.curPageIdx -= 1
        }
      }
      this.updateImg()
      this.updateMove()
    },
  },
  methods: {
    //refresh mobile height for css
    resize_book() {
      if (global.mode === 0) {
        document.documentElement.style.setProperty("--book_width", "1200rem");
        document.documentElement.style.setProperty("--book_height", "684.18rem");
        return
      }
      let scale_ratio = 700 / 752.6
      let height = global.height / global.width * 750
      document.documentElement.style.setProperty("--screen_height", height + "rem");
      let cal_width = scale_ratio * 1320
      if (cal_width > height - 170) {
        scale_ratio = (height - 170) / 1320
      }
      document.documentElement.style.setProperty("--book_width", 1320 * scale_ratio + "rem");
      document.documentElement.style.setProperty("--book_height", 752.6 * scale_ratio + "rem");
    },
    init() {
      let that = this
      let device
      if(global.mode === 0)
        device = "pc"
      else if(global.mode === 1)
        device = "mobile"
      // initial the page list
      for (let i = 1; i <= this.totalPageNum; i++) {
        this.pageList.push({
          L1: "/static/book/"+device+"/" + i + "/1.png",
          L2: "/static/book/"+device+"/" + i + "/2.png",
          L3: "/static/book/"+device+"/" + i + "/3.png",
          L4: "/static/book/"+device+"/" + i + "/4.png",
        })
      }
      this.updateImg()

      setTimeout(()=>{
        that.autoRightToLeft()
      },300)
    },
    prepareRightToLeft() {
      if (this.pageRotate === -180)
        this.pageRotate = 0
      this.pageRightSrc = this.pageList[this.curPageIdx + 1]
    },
    prepareLeftToRight() {
      if (this.pageRotate === 0)
        this.pageRotate = -180
      this.pageRightSrc = this.pageList[this.curPageIdx]
    },
    updateImg() {
      this.pageLeftSrc = this.pageList[this.curPageIdx]
      if (this.curPageIdx + 1 !== this.pageList.length) {
        this.pageRightSrc = this.pageList[this.curPageIdx + 1]
        this.pageRotateSrc = this.pageRotate >= -90 ? this.pageList[this.curPageIdx] : this.pageList[this.curPageIdx + 1]
      }
    },
    updateMove() {
      this.floatLeftValue = this.pageRotate / 180 * this.animeMaxLength
      this.floatRightValue = this.floatLeftValue + this.animeMaxLength
    },
    autoLeftToRight() {
      if (this.curPageIdx === 0 || this.tarPageIdx === 0 || flipAnimeTask != null)
        return
      this.tarPageIdx -= 1;
      this.prepareLeftToRight()
      flipAnimeTask = this.flipAnime(this.pageRotate, 0)
    },
    autoRightToLeft() {
      if (this.curPageIdx === this.pageList.length - 1 || this.tarPageIdx === this.pageList.length - 1 || flipAnimeTask != null)
        return
      this.tarPageIdx += 1;
      this.prepareRightToLeft()
      flipAnimeTask = this.flipAnime(this.pageRotate, -180)
    },
    flipAnime(start, end, time = 1000) {
      return anime({
        targets: this,
        pageRotate: [start, end],
        duration: time,
        easing: "linear",
        complete() {
          flipAnimeTask = null
        }
      });
    }

  },
};
</script>



